#navbar {
  position: fixed;
  top: 0px;
  right: 0px;
  width: 100%;
  z-index: 999;
}
.menu-box ul {
  background-color: #242227;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  padding: 10px 20px 10px 0;
  margin: 0;
}
.menu-box ul a {
  text-decoration: none;
  margin: 10px 10px 0 0;
  color: white;
  font-family: "Noto Sans", sans-serif;
  font-size: 22px;
  list-style-type: none;
  padding: 5px;
  transition: 0.2s;
  cursor: pointer;
}
.menu-box ul a:hover,
.mobile-box ul a:hover {
  color: #f0bebe;
  text-shadow: 0 0 10px #f0bebe, 2px 2px 2px rgba(240, 190, 190, 0);
}
#mobile-btn {
  position: absolute;
  top: 30px;
  right: 35px;
  z-index: 3;
  font-size: 4vh;
  font-weight: bold;
  color: #242227;
  border: 2px solid #242227;
  background-color: white;
  cursor: pointer;
  outline: none;
}
.mobile-box {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: #242227;
  opacity: 0.95;
  z-index: 2;
}
.mobile-box ul {
  margin: 0;
  padding: 0;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.mobile-box ul a {
  text-decoration: none;
  color: white;
  font-size: 56px;
  opacity: 1;
  margin-bottom: 20px;
  transition: 0.2s ease;
}

@keyframes slideIn {
  0% {
    margin-left: -100%;
  }
  100% {
    margin-left: 0;
  }
}
