#footer {
  background-color: #242227;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 20vh;
}
#footer p {
  color: #6c6775;
  text-align: center;
  padding: 10px;
  width: fit-content;
}
#footer p a {
  text-decoration: none;
  color: #6c6775;
}
#footer p img {
  cursor: pointer;
}
.cntower {
  filter: invert(40%) sepia(2%) saturate(2390%) hue-rotate(220deg)
    brightness(99%) contrast(87%);
  width: 15px;
  margin: 0 0 0 5px;
}
.cntower:hover {
  filter: invert(69%) sepia(33%) saturate(223%) hue-rotate(314deg)
    brightness(111%) contrast(89%);
}

@media only screen and (max-width: 800px) {
  #footer ul {
    justify-content: space-evenly;
  }
}
