#home {
  position: relative;
  height: 100vh;
  width: 100%;
}
#intro {
  position: absolute;
  bottom: 40%;
  left: 10%;
  pointer-events: none;
  max-width: 450px;
  min-width: auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
#intro h3,
#intro p {
  padding: 0;
  margin: 0;
  color: white;
  font-family: "Noto Sans", sans-serif;
  font-size: 4rem;
}
#intro p {
  font-size: 28px;
  letter-spacing: 0.33em;
  padding: 5px;
  width: fit-content;
}
#sidebar {
  position: absolute;
  top: 40%;
  right: 20px;
  width: 2.5vw;
  height: 20vh;
}
#sidebar ul,
#sidebar ul li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
#sidebar ul {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.sidebar-img img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: contain;
  filter: invert(100%) sepia(2%) saturate(7475%) hue-rotate(228deg)
    brightness(98%) contrast(111%);
  transition: 0.2s;
}
#sidebar ul li:hover .sidebar-img img {
  filter: invert(79%) sepia(5%) saturate(1859%) hue-rotate(314deg)
    brightness(106%) contrast(88%);
}
.slide-box {
  overflow: hidden;
  float: right;
  height: 65px;
  padding-top: 10px;
  margin-top: -5px;
  margin-right: 0px;
}
.flip {
  margin: 0;
  padding: 0;
  animation: flip 6s cubic-bezier(0.23, 1, 0.32, 1.1) infinite;
}
.flip li {
  color: #242227;
  background-color: white;
  font-family: "Noto Sans", sans-serif;
  font-size: 28px;
  letter-spacing: 0.38rem;
  padding: 0 10px;
  height: 40px;
  width: fit-content;
  margin-bottom: 50px;
  display: block;
}

@keyframes flip {
  0% {
    margin-top: -270px;
  }
  5% {
    margin-top: -180px;
  }
  33% {
    margin-top: -180px;
  }
  38% {
    margin-top: -90px;
  }
  66% {
    margin-top: -90px;
  }
  71% {
    margin-top: 0px;
  }
  99% {
    margin-top: 0px;
  }
  100% {
    margin-top: -270px;
  }
}
@media only screen and (max-width: 1000px) {
  #sidebar {
    top: 30px;
    left: 35px;
    min-width: 30vw;
    height: 5vh;
  }
  #sidebar ul {
    flex-direction: row;
    justify-content: space-between;
  }
  #sidebar ul li {
    width: 30px;
    margin-right: 10px;
  }
}

@media only screen and (max-width: 600px) {
  #intro {
    bottom: 25%;
  }
  #intro p {
    width: 100%;
  }
  .slide-box {
    width: 100%;
  }
}
