#contact {
  background-color: #dae1ed;
  padding: 15vh 0 15vh 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
#contact ul,
#contact ul li {
  display: flex;
  justify-content: center;
  margin: 10px;
  padding: 0;
}
.logo {
  width: 40px;
  filter: invert(11%) sepia(5%) saturate(1124%) hue-rotate(222deg)
    brightness(97%) contrast(92%);
}
.logo img {
  width: 100%;
  display: block;
  object-fit: contain;
}
#contact h1,
#contact p,
#contact button {
  font-family: "Noto Sans", sans-serif;
  color: #242227;
  text-align: center;
  margin: 10px;
}
#contact p {
  font-size: 18px;
}
#contact button {
  font-size: 18px;
  height: 30px;
  background-color: #dae1ed;
  border: 1px solid #242227;
  cursor: pointer;
}
#contact button:hover {
  color: #dae1ed;
  background-color: #242227;
}
#form {
  margin-top: 40px;
  min-width: 300px;
  width: 80%;
}
.send-form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: repeat(3, minmax(30px, auto-fit));
  grid-column-gap: 10px;
  grid-row-gap: 20px;
}
.send-form input,
.send-form textarea {
  font-family: "Noto Sans", sans-serif;
  font-size: 16px;
  border: none;
  border-bottom: 2px solid #242227;
  color: #242227;
  outline: none;
  min-height: 28px;
  background-color: transparent;
}
.send-form textarea {
  grid-area: 2/1/3/3;
  max-height: 500px;
  resize: vertical;
}
.send-form input:focus,
.send-form textarea:focus {
  border-bottom: 2px solid #1ae2ea;
}
.send-form #send {
  grid-area: 3/1/4/3;
  width: 200px;
  margin: auto;
  cursor: pointer;
}
.label-tags {
  text-align: end;
  font-family: "Noto Sans", sans-serif;
  font-size: 16px;
  align-self: start;
}
@media only screen and (max-width: 1000px) {
  #form {
    width: 90%;
  }
  .send-form {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, minmax(30px, auto-fit));
    grid-row-gap: 50px;
  }
  .send-form textarea,
  .send-form #send {
    grid-area: auto;
  }
}
