#bio {
  background-color: #242227;
  min-height: 30vh;
  padding-top: 20vh;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}
#bio-pic {
  width: fit-content;
  height: 70vh;
  position: relative;
}
.shadow {
  position: absolute;
  width: 100%;
  height: 100%;
  box-shadow: inset 0px 0px 30px 30px #242222;
  top: 0;
  left: 0;
}
#bio-pic img {
  border: 2px solid #242227;
  box-sizing: border-box;
  height: 100%;
  margin: auto;
  object-fit: contain;
  display: block;
}
#bio-text p {
  width: 50vw;
  text-align: start;
  padding: 0 20px;
  margin: 0;
  color: white;
  font-family: "Noto Sans", sans-serif;
  font-size: 22px;
  letter-spacing: 0.05em;
}
#bio-text p span {
  background-color: white;
  transition: 0.2s;
}
#bio-text p span:hover {
  background-color: #242227;
}
@media only screen and (max-width: 1000px) {
  #bio-text p {
    margin: 40px auto;
    width: 80%;
  }
}
@media only screen and (max-width: 600px) {
  #bio {
    flex-direction: column;
  }
  #bio-pic {
    width: 100%;
  }
  #bio-pic img {
    width: 90%;
  }
  #bio-text p {
    width: 85%;
  }
}
