* {
  font-family: "Noto Sans", sans-serif;
}
#skills {
  margin-top: 15vh;
  padding-top: 10vh;
  display: flex;
  flex-direction: column;
  min-height: 20vh;
}
#skills h1 {
  color: white;
  text-align: center;
  letter-spacing: 0.05em;
}

#skills-container {
  margin: 100px auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;

  width: 70%;
}
.skills-box {
  width: 180px;
  height: 140px;
  margin-bottom: 100px;
  display: flex;
  flex-direction: column;
  transition: 0.3s ease;
}
.skills-box:hover {
  filter: invert(29%) sepia(95%) saturate(7052%) hue-rotate(266deg)
    brightness(94%) contrast(93%);
}
.skills-box p {
  width: 100%;
  height: 20%;
  color: white;
  text-align: center;
  font-size: 18px;
  text-transform: uppercase;
}
.skills-image {
  height: 80%;
  width: 100%;
}
.skills-image img {
  display: block;
  object-fit: contain;
  width: 100%;
  height: 100%;
  filter: invert(100%) sepia(0%) saturate(0%) hue-rotate(129deg)
    brightness(110%) contrast(101%);
}
