#work {
  clip-path: polygon(0 3%, 0 100%, 100% 100%, 100% 0);
  background-color: #dae1ed;
  min-height: 20vh;
  padding-top: 15vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
#work h1 {
  text-align: center;
  color: #242227;
  font-family: "Noto Sans", sans-serif;
  letter-spacing: 0.05em;
  margin-top: 30px;
}
#work h2 {
  text-align: center;
  color: white;
  font-family: "Noto Sans", sans-serif;
  letter-spacing: 0.05em;
  font-weight: 400;
}
.card-box {
  width: 100%;
  margin: 40px 0 80px 0;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
.card {
  position: relative;
  min-width: 320px;
  width: 40%;
  height: 420px;
  background-color: white;
  transition: 0.2s ease-in-out;
  margin: 30px;
}
.card:hover {
  transform: scale(1.1);
  box-shadow: 1px 1px 18px #242227;
}
.card img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.card .project-info {
  position: absolute;
  top: 0;
  left: 0;
  background-image: linear-gradient(
    0deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(0, 0, 0, 0) 35%
  );
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  transition: 0.2s;
}
.card .project-info:hover {
  background-color: rgba(0, 0, 0, 0.8);
}
.card .project-info .text {
  display: none;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}
.card .project-info .text p {
  width: 95%;
  font-family: "Noto Sans", sans-serif;
  color: white;
  padding: 10px;
}
.card .project-info .text a {
  text-decoration: none;
  color: white;
  width: 40%;
  text-align: center;
  border: 1px solid white;
  margin: 10px;
  transition: 0.2s;
}
.card .project-info .text a:hover {
  background-color: white;
  color: #242227;
}

@media only screen and (max-width: 1000px) {
  .card {
    width: 80%;
    height: 320px;
    background-color: white;
    transition: 0.2s ease-in-out;
    margin: 30px;
  }
}
@keyframes slideUp {
  0% {
    margin-bottom: -100%;
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    margin-bottom: 0%;
    opacity: 1;
  }
}
